import React from "react";
import {Link} from "react-router-dom";
import {Nav} from 'react-bootstrap';
import logo from "../images/logo.jpg";

export default function NavigationBarFull(){

    return( 
      <>
        <nav className="navbar navbar-fixed shadow navbar-expand-lg navbar-light bg-light mb-3">
            <div className="container-fluid wrapper-ersiees">
              <a className="navbar-brand" href="/"><img src={logo} width="40" height="30" alt="er-sie-es.com" /><b style={{marginLeft: '5px'}}>{'AutoSense'}</b></a>
              <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
              </button>
              <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav me-auto mb-2 mb-lg-0">     
                  <li className="nav-item">
                    <Link className="nav-link" to={{pathname:"/quantityEstimation"}}>Quantity Estimation</Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to={{pathname:"/chat"}}>Chat</Link>
                  </li>
                </ul>
              </div>
            </div>
          </nav>  
          <div className="mb-3" style={{minHeight: "59px"}}></div>   
          </> 
    )}